// import yzkkEditor from '@/template/editor/editor.vue'
import uploadImg from '@/template/uploadImg.vue'
export default {
	components:{
		// yzkkEditor,
		uploadImg
	},
	data(){
		return{
			    readOnly: false,
				formats: {},
			formData:{
				title:'',
				content:'',
				imageList:[]
			},
		}
	},
	methods: {
		submitForm(){
			var that=this;
			this.formData.imageList=this.$refs.uploadImg.returnImageList()
			this.$commonJs.uniMsg('发布成功','success')
			setTimeout(function(){
				that.navBack()
			},1000)
		},
		navBack(){
			this.$commonJs.navBack();
		}	
	}
}
